<template>
    <div class="page-article" v-if="model">
        <div class="d-flex p-2">
            <router-link  to="/"> BACK | </router-link>
            <div class="flex-1">
                {{model.title}}
            </div>
            <div class="text-grey fs-sx">2019-06-06</div>
        </div>
        <div v-html="model.body" class="p-3"></div>
    </div>
</template>

<script>
export default {
    name: 'Article',
    props: {
        id:{require:true}     
    },
    data() {
        return {
            model:null
        }
    },
    methods: {
        async fetch(){
            const res = await this.$http.get(`/articles/${this.id}`);
         
            this.model = res.data;
           
        }
    },
    created() {
        this.fetch()
    },
}
</script>


